"use client";

import { useState, useEffect } from "react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import { Switch } from "@/components/ui/switch";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";

export default function DashboardPage() {
  // --- STATE ---
  const [activeTab, setActiveTab] = useState("akun");
  const [activeReportTab, setActiveReportTab] = useState("sebaran");

  // State untuk alur "Atur Anggaran"
  const [showBudgetModal, setShowBudgetModal] = useState(false);
  const [budgetAmount, setBudgetAmount] = useState("");
  const [budgetPeriod, setBudgetPeriod] = useState("Bulan ini");
  const [repeatBudget, setRepeatBudget] = useState(false);

  // State untuk alur "Tambah Akun"
  const [showAccountTypeModal, setShowAccountTypeModal] = useState(false);
  const [showBankSelectionModal, setShowBankSelectionModal] = useState(false);
  const [showInitialBalanceModal, setShowInitialBalanceModal] = useState(false);
  const [selectedBank, setSelectedBank] = useState<{ name: string; icon: string } | null>(null);
  const [initialBalance, setInitialBalance] = useState("");
  const [isTargetSet, setIsTargetSet] = useState(false);
  const [targetBalance, setTargetBalance] = useState("");
  const [targetPeriod, setTargetPeriod] = useState("1 Bulan");
  
  // State untuk Modal Bersama
  const [showCategoryModal, setShowCategoryModal] = useState(false);
  const [modalFlowContext, setModalFlowContext] = useState<"budget" | "transaction" | "transfer" | null>(null);
  const [accountSelectionContext, setAccountSelectionContext] = useState<'source' | 'destination' | null>(null);

  // State untuk alur "Catat Transaksi" & "Transfer"
  const [showSelectTransactionTypeModal, setShowSelectTransactionTypeModal] = useState(false);
  const [showSelectAccountForTxModal, setShowSelectAccountForTxModal] = useState(false);
  const [showRecordTransactionModal, setShowRecordTransactionModal] = useState(false);
  const [showTransferModal, setShowTransferModal] = useState(false);
  const [newTransactionData, setNewTransactionData] = useState<any>({});
  const [isRecurringTx, setIsRecurringTx] = useState(false);
  const [recurringTxPeriod, setRecurringTxPeriod] = useState("1 Bulan");
  
  // --- DYNAMIC DATA STATES ---
  const [existingAccounts, setExistingAccounts] = useState<any[]>([]);
  const [transactionHistory, setTransactionHistory] = useState<any[]>([]);
  const [bankList, setBankList] = useState<any[]>([]);
  const [budgetPeriods, setBudgetPeriods] = useState<any[]>([]);
  const [categoryList, setCategoryList] = useState<any[]>([]);

  // Fetch existingAccounts from API
  useEffect(() => {
    async function fetchExistingAccounts() {
      try {
        const response = await fetch('/api/accounts');
        if (response.ok) {
          const data = await response.json();
          setExistingAccounts(data);
        } else {
          console.error("Failed to fetch accounts");
        }
      } catch (error) {
        console.error("Error fetching accounts:", error);
      }
    }
    fetchExistingAccounts();
  }, []);

  // Fetch bankList from API
  useEffect(() => {
    async function fetchBankList() {
      try {
        const response = await fetch('/api/banks');
        if (response.ok) {
          const data = await response.json();
          setBankList(data);
        } else {
          console.error("Failed to fetch banks");
        }
      } catch (error) {
        console.error("Error fetching banks:", error);
      }
    }
    fetchBankList();
  }, []);

  // Fetch budgetPeriods from API
  useEffect(() => {
    async function fetchBudgetPeriods() {
      try {
        const response = await fetch('/api/budgetPeriods');
        if (response.ok) {
          const data = await response.json();
          setBudgetPeriods(data);
        } else {
          console.error("Failed to fetch budget periods");
        }
      } catch (error) {
        console.error("Error fetching budget periods:", error);
      }
    }
    fetchBudgetPeriods();
  }, []);

  // Fetch categoryList from API
  useEffect(() => {
    async function fetchCategoryList() {
      try {
        const response = await fetch('/api/categories');
        if (response.ok) {
          const data = await response.json();
          setCategoryList(data);
        } else {
          console.error("Failed to fetch categories");
        }
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    }
    fetchCategoryList();
  }, []);

  // --- HANDLERS ---
  const handleOpenBudgetFlow = () => {
    setModalFlowContext("budget");
    setShowCategoryModal(true);
  };
  
  const handleOpenTransactionFlow = () => {
    setNewTransactionData({});
    setShowSelectTransactionTypeModal(true);
  };
  
  const handleSelectTransactionType = (type: string) => {
    setShowSelectTransactionTypeModal(false);
    setNewTransactionData({ type });
    if (type === 'Transfer') {
      setModalFlowContext('transfer');
      setShowTransferModal(true);
    } else {
      setModalFlowContext("transaction");
      setShowCategoryModal(true);
    }
  };

  const handleCategorySelect = (category: { id: number; name: string; icon: string }) => {
    setShowCategoryModal(false);
    setNewTransactionData((prev: any) => ({ ...prev, category }));
    if (modalFlowContext === "budget") {
      setShowBudgetModal(true);
    } else {
      setShowSelectAccountForTxModal(true);
    }
  };

  const handleAccountSelect = (account: any) => {
    setShowSelectAccountForTxModal(false);
    if (modalFlowContext === 'transaction') {
      setNewTransactionData((prev: any) => ({ ...prev, account }));
      setShowRecordTransactionModal(true);
    } else if (modalFlowContext === 'transfer') {
      if (accountSelectionContext === 'source') {
        setNewTransactionData((prev: any) => ({ ...prev, sourceAccount: account }));
      } else if (accountSelectionContext === 'destination') {
        setNewTransactionData((prev: any) => ({ ...prev, destinationAccount: account }));
      }
    }
  };

  const handleOpenAccountSelectorForTransfer = (context: 'source' | 'destination') => {
    setAccountSelectionContext(context);
    setShowSelectAccountForTxModal(true);
  };
  
  const handleSaveTransaction = () => {
      const finalData = { ...newTransactionData, amount: (document.getElementById('txAmount') as HTMLInputElement)?.value, description: (document.getElementById('txDescription') as HTMLInputElement)?.value, date: '4 Juli 2025', isRecurring: isRecurringTx, recurringPeriod: isRecurringTx ? recurringTxPeriod : null };
      console.log("TRANSAKSI BARU DISIMPAN:", finalData);
      setShowRecordTransactionModal(false);
      setNewTransactionData({});
      setIsRecurringTx(false);
  };
  
  const handleSaveTransfer = () => {
      const finalData = { ...newTransactionData, amount: (document.getElementById('transferAmount') as HTMLInputElement)?.value, description: (document.getElementById('transferDescription') as HTMLInputElement)?.value, date: '4 Juli 2025', isRecurring: isRecurringTx, recurringPeriod: isRecurringTx ? recurringTxPeriod : null };
      console.log("TRANSFER BARU DISIMPAN:", finalData);
      setShowTransferModal(false);
      setNewTransactionData({});
      setIsRecurringTx(false);
  }

  const handleAccountTypeSelect = (type: string) => {
    setShowAccountTypeModal(false);
    if (type === 'Bank') setShowBankSelectionModal(true);
  };
  
  const handleBankSelect = (bank: { name: string; icon: string }) => {
    setSelectedBank(bank);
    setShowBankSelectionModal(false);
    setShowInitialBalanceModal(true);
  };
  
  const handleSaveNewAccount = () => {
    console.log("AKUN BARU DISIMPAN:", { bank: selectedBank, initialBalance, isTargetSet, targetBalance, targetPeriod });
    setShowInitialBalanceModal(false);
  };

  return (
    <main className="max-w-md mx-auto p-4 bg-gray-50 min-h-screen flex flex-col">
      {/* Header */}
      <div className="bg-white rounded-xl p-4 mb-6 shadow-sm">
        <header className="flex justify-between items-center">
          <div>
            <h1 className="text-xl font-semibold">Dompet Digital Cerdas</h1>
            <p className="text-gray-600">Hi, <strong>Admin</strong>!</p>
          </div>
          <div className="w-10 h-10 rounded-full bg-blue-700 flex items-center justify-center text-white font-bold">A</div>
        </header>
      </div>
      
      {/* Tabs */}
      <div className="bg-white rounded-xl p-4 mb-6 shadow-sm">
        <div className="flex bg-gray-200 rounded-full mb-4">
          <button className={`flex-1 py-2 rounded-full font-semibold ${activeTab === "saldo" ? "bg-blue-700 text-white" : "text-gray-600"}`} onClick={() => setActiveTab("saldo")}>Total Saldo</button>
          <button className={`flex-1 py-2 rounded-full font-semibold ${activeTab === "akun" ? "bg-blue-700 text-white" : "text-gray-600"}`} onClick={() => setActiveTab("akun")}>Daftar Akun</button>
        </div>

        {activeTab === "saldo" && (
          <section><p className="text-gray-600 mb-1">Total Saldo</p><p className="text-3xl font-bold">Rp 895.252</p></section>
        )}
        
        {activeTab === "akun" && (
          <>
            <button className="w-full border border-dashed border-gray-400 rounded-lg py-3 text-gray-600 font-semibold mb-4" onClick={() => setShowAccountTypeModal(true)}>+ Tambah Akun Baru</button>
            {existingAccounts.map(acc => (
              <div key={acc.name} className="flex items-center gap-3 bg-gray-100 rounded-lg p-3 mb-2">
                <div className="w-10 h-10 bg-white rounded-lg flex items-center justify-center text-2xl">{acc.icon}</div>
                <div><p className="font-semibold">{acc.name}</p><p className="text-black font-bold">{acc.balance}</p></div>
              </div>
            ))}
          </>
        )}
      </div>
      
      {/* --- KONTEN DASBOR DARI KODE 2 --- */}
      <section className="bg-blue-700 text-white rounded-lg p-4 mb-6 shadow-sm"><h2 className="font-semibold text-lg mb-2">Saran Aksi</h2><p className="mb-4 text-sm leading-relaxed">Pengeluaran untuk kategori Makanan bulan ini (Rp 35.000) lebih tinggi dari rata-rata. Pertimbangkan untuk mengurangi jajan di luar.</p><button className="bg-white text-blue-700 font-semibold py-2 rounded w-full">Lihat Detail Anggaran</button></section>

      <section className="flex gap-4 mb-6">
        <div className="flex-1 bg-white rounded-xl p-4 text-center shadow-sm"><p className="text-gray-500 text-sm mb-1">Pemasukan Bulan Ini</p><p className="text-green-600 font-bold text-xl">Rp 5.000.000</p></div>
        <div className="flex-1 bg-white rounded-xl p-4 text-center shadow-sm"><p className="text-gray-500 text-sm mb-1">Pengeluaran Bulan Ini</p><p className="text-red-600 font-bold text-xl">Rp 55.000</p></div>
      </section>

      <section className="mb-6 bg-white rounded-xl p-4 shadow-sm">
        <h3 className="font-semibold mb-4">Anggaran Bulan Ini</h3>
        <button className="w-full border border-dashed border-gray-400 rounded-lg py-3 text-gray-600 font-semibold mb-4" onClick={handleOpenBudgetFlow}>+ Atur Anggaran Baru</button>
        <div className="mb-1 flex items-center gap-2 text-sm"><span>üçî</span><span>Makanan</span></div>
        <div className="flex items-center gap-2"><div className="flex-1 bg-gray-200 rounded-full h-2.5 relative"><div className="bg-red-500 h-2.5 rounded-full" style={{ width: "70%" }}></div></div></div>
        <p className="text-xs text-gray-600 mt-1 text-right">Rp 35.000 / Rp 50.000</p>
      </section>

      <section className="mb-6 bg-white rounded-xl p-4 shadow-sm">
        <h3 className="font-semibold mb-4">Laporan Keuangan</h3>
        <div className="flex border-b border-gray-200 mb-4">
          <button className={`px-4 py-2 font-semibold ${activeReportTab === "sebaran" ? "text-blue-700 border-b-2 border-blue-700" : "text-gray-500"}`} onClick={() => setActiveReportTab("sebaran")}>Sebaran</button>
          <button className={`px-4 py-2 font-semibold ${activeReportTab === "tren" ? "text-blue-700 border-b-2 border-blue-700" : "text-gray-500"}`} onClick={() => setActiveReportTab("tren")}>Tren</button>
        </div>
        {activeReportTab === "sebaran" && (
          <div className="flex flex-col items-center w-full"><svg className="w-32 h-32" viewBox="0 0 100 100" fill="none" xmlns="http://www.w3.org/2000/svg"><circle cx="50" cy="50" r="40" stroke="#fca5a5" strokeWidth="20" /><circle cx="50" cy="50" r="40" stroke="#86efac" strokeWidth="20" strokeDasharray="251.2" strokeDashoffset="10" transform="rotate(-90 50 50)"/></svg><div className="flex flex-wrap justify-center gap-4 mt-4 text-xs"><div className="flex items-center gap-2"><div className="w-3 h-3 bg-red-400 rounded-sm"></div><span>Pengeluaran</span></div><div className="flex items-center gap-2"><div className="w-3 h-3 bg-green-400 rounded-sm"></div><span>Pemasukan</span></div></div></div>
        )}
        {activeReportTab === "tren" && (
          <div className="w-full h-40 flex items-end justify-around text-center"><div className="flex flex-col items-center"><div className="w-8 bg-green-400" style={{height: '100%'}}></div><p className="text-xs mt-1">Pemasukan</p></div><div className="flex flex-col items-center"><div className="w-8 bg-red-400" style={{height: '15%'}}></div><p className="text-xs mt-1">Pengeluaran</p></div></div>
        )}
      </section>

      <section className="mb-24 bg-white rounded-xl p-4 shadow-sm">
        <div className="flex justify-between items-center mb-3"><h3 className="font-semibold">Riwayat Transaksi</h3><a href="#" className="text-blue-700 font-semibold text-sm">Lihat Semua &rarr;</a></div>
        {transactionHistory.map(tx => (
            <div key={tx.id} className="flex items-center gap-3 p-2 rounded-lg hover:bg-gray-50">
                <div className="w-10 h-10 bg-gray-100 rounded-full flex items-center justify-center text-2xl">{tx.icon}</div>
                <div className="flex-1"><p className="font-semibold">{tx.category}</p><p className="text-gray-500 text-sm">{tx.account}</p></div>
                <div className={`font-semibold ${tx.amount.startsWith('+') ? 'text-green-600' : 'text-red-600'}`}>{tx.amount}</div>
                <div className="text-gray-400 text-xs w-10 text-right">{tx.date}</div>
            </div>
        ))}
      </section>
      
      {/* --- BOTTOM NAVIGATION --- */}
      <nav className="fixed bottom-0 left-0 right-0 bg-transparent flex justify-center py-4 text-gray-600 text-xs font-semibold">
        <div className="relative w-[360px] h-16 bg-white rounded-full shadow-lg flex items-center justify-around px-4">
          <button className="flex items-center justify-center h-full text-gray-600 w-12"><span className="text-2xl">üì∑</span></button>
          <button className="bg-blue-700 text-white rounded-full px-6 py-3 font-semibold shadow-md flex-1 mx-2" onClick={handleOpenTransactionFlow}>Catat Transaksi</button>
          <button className="flex items-center justify-center h-full text-gray-600 w-12"><span className="text-2xl">üé§</span></button>
        </div>
      </nav>

      {/* --- SEMUA MODAL DITEMPATKAN DI SINI --- */}
      <Dialog open={showSelectTransactionTypeModal} onOpenChange={setShowSelectTransactionTypeModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Pilih Transaksi</DialogTitle></DialogHeader><div className="flex justify-center flex-wrap gap-4 mt-4"><button onClick={() => handleSelectTransactionType('Pengeluaran')} className="px-6 py-3 rounded-lg bg-gray-100 hover:bg-gray-200 font-semibold">Pengeluaran</button><button onClick={() => handleSelectTransactionType('Pemasukan')} className="px-6 py-3 rounded-lg bg-gray-100 hover:bg-gray-200 font-semibold">Pemasukan</button><button onClick={() => handleSelectTransactionType('Transfer')} className="px-6 py-3 rounded-lg bg-gray-100 hover:bg-gray-200 font-semibold">Transfer</button></div></DialogContent></Dialog>
      <Dialog open={showTransferModal} onOpenChange={setShowTransferModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Transfer antar Akun</DialogTitle></DialogHeader><div className="text-center my-2 font-semibold text-gray-700 cursor-pointer">Hari ini, 4 Juli 2025 ‚åÑ</div><div className="flex items-center justify-around my-4"><button onClick={() => handleOpenAccountSelectorForTransfer('source')} className="flex flex-col items-center gap-2 text-center"><div className="w-16 h-16 rounded-full bg-gray-100 flex items-center justify-center text-3xl border-2 border-dashed">{newTransactionData.sourceAccount ? newTransactionData.sourceAccount.icon : '?'}</div><span className="font-semibold text-sm">{newTransactionData.sourceAccount ? newTransactionData.sourceAccount.name : 'Akun 1'}</span></button><span className="text-2xl text-gray-400 font-light">&rarr;</span><button onClick={() => handleOpenAccountSelectorForTransfer('destination')} className="flex flex-col items-center gap-2 text-center"><div className="w-16 h-16 rounded-full bg-gray-100 flex items-center justify-center text-3xl border-2 border-dashed">{newTransactionData.destinationAccount ? newTransactionData.destinationAccount.icon : '?'}</div><span className="font-semibold text-sm">{newTransactionData.destinationAccount ? newTransactionData.destinationAccount.name : 'Akun 2'}</span></button></div><div className="space-y-4 mt-4"><div><Input id="transferAmount" type="number" placeholder="Rp 0" className="text-center text-2xl h-14 font-bold"/></div><div><Input id="transferDescription" placeholder="Tulis keterangan jika diperlukan" /></div><div className="flex items-center space-x-2 pt-4"><Checkbox id="setRecurringTransfer" checked={isRecurringTx} onCheckedChange={() => setIsRecurringTx(!isRecurringTx)} /><Label htmlFor="setRecurringTransfer" className="font-medium">Set pengingat untuk transfer rutin</Label></div>{isRecurringTx && (<div className="p-4 border rounded-lg flex flex-wrap gap-2 justify-center animate-in fade-in-0 duration-300"><button onClick={() => setRecurringTxPeriod('1 Minggu')} className={`px-3 py-1 text-sm rounded-full ${recurringTxPeriod === '1 Minggu' ? 'bg-blue-700 text-white' : 'bg-gray-200'}`}>1 Minggu</button><button onClick={() => setRecurringTxPeriod('1 Bulan')} className={`px-3 py-1 text-sm rounded-full ${recurringTxPeriod === '1 Bulan' ? 'bg-blue-700 text-white' : 'bg-gray-200'}`}>1 Bulan</button><button onClick={() => setRecurringTxPeriod('1 Tahun')} className={`px-3 py-1 text-sm rounded-full ${recurringTxPeriod === '1 Tahun' ? 'bg-blue-700 text-white' : 'bg-gray-200'}`}>1 Tahun</button></div>)}</div><DialogFooter className="mt-6 flex gap-2"><DialogClose asChild><button type="button" className="bg-gray-200 text-gray-800 font-semibold py-2 px-4 rounded-lg w-full">Batal</button></DialogClose><button onClick={handleSaveTransfer} className="bg-green-500 text-white font-semibold py-2 px-4 rounded-lg w-full">Simpan</button></DialogFooter></DialogContent></Dialog>
      <Dialog open={showSelectAccountForTxModal} onOpenChange={setShowSelectAccountForTxModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Pilih Akun</DialogTitle></DialogHeader><div className="flex flex-col gap-2 mt-4">{existingAccounts.map(acc => (<button key={acc.name} onClick={() => handleAccountSelect(acc)} className="w-full text-left p-3 hover:bg-gray-100 rounded-lg flex items-center justify-between"><div className="flex items-center gap-3"><span className="text-2xl">{acc.icon}</span><span className="font-semibold">{acc.name}</span></div><span className="font-bold text-sm">{acc.balance}</span></button>))}</div></DialogContent></Dialog>
      <Dialog open={showRecordTransactionModal} onOpenChange={setShowRecordTransactionModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Catat Transaksi</DialogTitle>{newTransactionData.category && newTransactionData.account && <DialogDescription>{newTransactionData.type} untuk <strong>{newTransactionData.category.name}</strong> dari akun <strong>{newTransactionData.account.name}</strong>.</DialogDescription>}</DialogHeader><div className="text-center my-2 font-semibold text-gray-700 cursor-pointer">Hari ini, 4 Juli 2025 ‚åÑ</div><div className="space-y-4 mt-4"><div><Input id="txAmount" type="number" placeholder="Rp 0" className="text-center text-2xl h-14 font-bold" /></div><div><Input id="txDescription" placeholder="Tulis keterangan jika diperlukan"/></div><div className="flex items-center space-x-2 pt-4"><Checkbox id="setRecurring" checked={isRecurringTx} onCheckedChange={() => setIsRecurringTx(!isRecurringTx)} /><Label htmlFor="setRecurring" className="font-medium">Set pengingat untuk transaksi rutin</Label></div></div><DialogFooter className="mt-6 flex gap-2"><DialogClose asChild><button type="button" className="bg-gray-200 text-gray-800 font-semibold py-2 px-4 rounded-lg w-full">Batal</button></DialogClose><button onClick={handleSaveTransaction} className="bg-green-500 text-white font-semibold py-2 px-4 rounded-lg w-full">Simpan</button></DialogFooter></DialogContent></Dialog>
      <Dialog open={showCategoryModal} onOpenChange={setShowCategoryModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Pilih Kategori</DialogTitle></DialogHeader><div className="grid grid-cols-4 gap-4 mt-4">{categoryList.map((category) => (<button key={category.id} onClick={() => handleCategorySelect(category)} className="flex flex-col items-center gap-2 p-4 text-center hover:bg-gray-100 rounded-lg"><div className="flex h-12 w-12 items-center justify-center text-2xl">{category.icon}</div><span className="text-xs">{category.name}</span></button>))}<button onClick={() => { setShowCategoryModal(false); }} className="flex flex-col items-center gap-2 p-4 text-center hover:bg-gray-100 rounded-lg"><div className="flex h-12 w-12 items-center justify-center text-2xl font-bold">+</div><span className="text-xs">Buat Baru</span></button></div></DialogContent></Dialog>
      <Dialog open={showBudgetModal} onOpenChange={setShowBudgetModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Tambah Anggaran</DialogTitle><DialogDescription>Atur anggaran untuk kategori yang Anda pilih.</DialogDescription></DialogHeader><div className="mt-4">{newTransactionData.category && (<div className="flex items-center gap-3 bg-gray-100 rounded-lg p-3 mb-4"><div className="w-10 h-10 bg-white rounded-lg flex items-center justify-center text-2xl">{newTransactionData.category.icon}</div><div><p className="font-semibold">{newTransactionData.category.name}</p><p className="text-sm text-gray-500">Kategori Anggaran</p></div></div>)}<div className="mb-4"><Label htmlFor="budgetAmount" className="text-sm font-medium">Jumlah Anggaran</Label><Input id="budgetAmount" type="number" value={budgetAmount} onChange={(e) => setBudgetAmount(e.target.value)} placeholder="Rp 0" className="mt-1"/></div><div className="mb-4"><Label htmlFor="budgetPeriod" className="text-sm font-medium">Periode</Label><Select onValueChange={setBudgetPeriod} defaultValue={budgetPeriod}><SelectTrigger id="budgetPeriod" className="mt-1"><SelectValue placeholder="Pilih periode" /></SelectTrigger><SelectContent>{budgetPeriods.map(period => (<SelectItem key={period.label} value={period.label}>{period.label} ({period.dateRange})</SelectItem>))}</SelectContent></Select></div><div className="flex items-center justify-between"><Label htmlFor="repeatBudget" className="text-sm font-medium">Ulangi anggaran ini</Label><Switch id="repeatBudget" checked={repeatBudget} onCheckedChange={setRepeatBudget}/></div></div><DialogFooter className="mt-6 flex-row justify-end gap-2"><DialogClose asChild><button type="button" className="bg-gray-200 text-gray-800 font-semibold py-2 px-4 rounded-lg">Batal</button></DialogClose><button type="button" onClick={() => { setShowBudgetModal(false); }} className="bg-blue-700 text-white font-semibold py-2 px-4 rounded-lg">Simpan</button></DialogFooter></DialogContent></Dialog>
      <Dialog open={showAccountTypeModal} onOpenChange={setShowAccountTypeModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Pilih Tipe Akun</DialogTitle></DialogHeader><div className="grid grid-cols-4 gap-4 mt-4"><button onClick={() => handleAccountTypeSelect('Bank')} className="flex flex-col items-center gap-2 p-4 text-center hover:bg-gray-100 rounded-lg"><div className="flex h-12 w-12 items-center justify-center text-2xl">üè¶</div><span className="text-xs">Bank</span></button><button onClick={() => {}} className="flex flex-col items-center gap-2 p-4 text-center hover:bg-gray-100 rounded-lg"><div className="flex h-12 w-12 items-center justify-center text-2xl">üíµ</div><span className="text-xs">Tunai</span></button><button onClick={() => {}} className="flex flex-col items-center gap-2 p-4 text-center hover:bg-gray-100 rounded-lg"><div className="flex h-12 w-12 items-center justify-center text-2xl">üí≥</div><span className="text-xs">Kartu Kredit</span></button><button className="flex flex-col items-center gap-2 p-4 text-center hover:bg-gray-100 rounded-lg"><div className="flex h-12 w-12 items-center justify-center text-2xl font-bold">+</div><span className="text-xs">Buat Baru</span></button></div></DialogContent></Dialog>
      <Dialog open={showBankSelectionModal} onOpenChange={setShowBankSelectionModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Pilih Bank</DialogTitle></DialogHeader><div className="flex flex-col gap-2 mt-4">{bankList.map(bank => (<button key={bank.name} onClick={() => handleBankSelect(bank)} className="w-full text-left p-3 hover:bg-gray-100 rounded-lg flex items-center gap-3"><span className="text-2xl">{bank.icon}</span><span className="font-semibold">{bank.name}</span></button>))}<button onClick={() => {}} className="w-full text-left p-3 hover:bg-gray-100 rounded-lg flex items-center gap-3 border-t mt-2 pt-4"><span className="text-2xl font-bold w-6 text-center">+</span><span className="font-semibold">Buat Baru</span></button></div></DialogContent></Dialog>
      <Dialog open={showInitialBalanceModal} onOpenChange={setShowInitialBalanceModal}><DialogContent className="w-[90%] sm:w-full sm:max-w-md rounded-lg"><DialogHeader><DialogTitle>Atur Saldo Akun</DialogTitle>{selectedBank && <DialogDescription>Menambahkan akun untuk <strong>{selectedBank.name}</strong>.</DialogDescription>}</DialogHeader><div className="space-y-4 mt-4"><div><Label htmlFor="initialBalance">Saldo Awal</Label><Input id="initialBalance" type="number" placeholder="Rp 1.000.000" value={initialBalance} onChange={e => setInitialBalance(e.target.value)} className="mt-1"/></div><div className="flex items-center space-x-2"><Checkbox id="setTarget" checked={isTargetSet} onCheckedChange={() => setIsTargetSet(!isTargetSet)} /><Label htmlFor="setTarget" className="font-medium">Set Target tabungan</Label></div>{isTargetSet && (<div className="p-4 border rounded-lg space-y-4 animate-in fade-in-0 duration-300"><div><Label htmlFor="targetBalance">Saldo Target</Label><Input id="targetBalance" type="number" placeholder="Rp 10.000.000" value={targetBalance} onChange={e => setTargetBalance(e.target.value)} className="mt-1"/></div><div className="flex flex-wrap gap-2"><button onClick={() => setTargetPeriod('1 Minggu')} className={`px-3 py-1 text-sm rounded-full ${targetPeriod === '1 Minggu' ? 'bg-blue-700 text-white' : 'bg-gray-200'}`}>1 Minggu</button><button onClick={() => setTargetPeriod('1 Bulan')} className={`px-3 py-1 text-sm rounded-full ${targetPeriod === '1 Bulan' ? 'bg-blue-700 text-white' : 'bg-gray-200'}`}>1 Bulan</button><button onClick={() => setTargetPeriod('1 Tahun')} className={`px-3 py-1 text-sm rounded-full ${targetPeriod === '1 Tahun' ? 'bg-blue-700 text-white' : 'bg-gray-200'}`}>1 Tahun</button></div></div>)}</div><DialogFooter className="mt-6 flex gap-2"><DialogClose asChild><button type="button" className="bg-gray-200 text-gray-800 font-semibold py-2 px-4 rounded-lg w-full">Batal</button></DialogClose><button onClick={handleSaveNewAccount} className="bg-green-500 text-white font-semibold py-2 px-4 rounded-lg w-full">Simpan</button></DialogFooter></DialogContent></Dialog>

    </main>
  );
}